.intro .banner {
  width: 100%;
  height: 1000px;
  background: url(../img/intro-bg.png) no-repeat 50% 0;
  background-size: 100% 100%;
}

.intro .logo {
  display: block;
  margin-top: 40px;
  width: 250px;
  height: 60px;
  background: url(../img/logo.png) no-repeat;
}

.intro .headline {
  display: block;
  margin: 80px auto 0;
  width: 950px;
  height: 285px;
}

.product {
  position: relative;
  margin: -400px auto 0;
  width: 1200px;
  height: 660px;
  background: url(../img/product.png) no-repeat;
  background-size: 100% 100%;
}

.product .teacher {
  position: absolute;
  top: 0;
  left: 167px;
  z-index: 1;
  width: 840px;
  height: 560px;
}

.product .teacher .tooltip {
  position: absolute;
  top: 340px;
  left: -40px;
  width: 100px;
  height: 50px;
  font-size: 20px;
  line-height: 50px;
  text-align: center;
  color: #0d202f;
  border-radius: 25px 40px 0 25px;
  background: #fff;
  box-shadow: 0 0 5px #cddeec;
}

.product .student {
  position: absolute;
  top: 200px;
  right: 65px;
  z-index: 2;
  width: 270px;
  height: 390px;
}

.product .student .tooltip {
  position: absolute;
  top: 130px;
  left: 260px;
  width: 100px;
  height: 50px;
  font-size: 20px;
  line-height: 50px;
  text-align: center;
  color: #0d202f;
  border-radius: 40px 25px 25px 0;
  background: #fff;
  box-shadow: 0 0 5px #cddeec;
}

.intro-list {
  padding: 40px 0;
  height: 230px;
}

.intro-list li {
  display: inline-block;
  width: 33.33%;
  text-align: center;
}

.intro-item img {
  width: 140px;
  height: 120px;
}

.intro-item h3 {
  margin-top: 15px;
  font-size: 22px;
  line-height: 40px;
  color: #142c44;
}

.intro-item p {
  font-size: 14px;
  line-height: 25px;
  color: #636a7f;
}

@media only screen and (max-width: 1400px) {
  .intro .headline {
    width: 800px;
    height: 240px;
  }

  .product {
    width: 800px;
    height: 440px;
  }

  .product .teacher {
    left: 110px;
    width: 560px;
    height: 380px;
  }

  .product .teacher .tooltip {
    top: 200px;
  }

  .product .student {
    top: 133px;
    right: 42px;
    width: 182px;
    height: 260px;
  }

  .product .student .tooltip {
    left: 175px;
  }
}