html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}

body {
  font-size: 14px;
  line-height: 1.5;
  font-family: "Segoe UI", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #333;
  background: #fff;
}

.wrapper {
  margin: 0 auto;
  max-width: 1200px;
}

.title {
  position: relative;
  z-index: 1;
  font-size: 40px;
  line-height: 1;
  color: #142c44;
  text-align: center;
}

.title:before {
  content: attr(data-text);
  position: absolute;  
  z-index: -1;
  margin:-40px 0 0 -80px;
  font-size: 80px;
  line-height: 1;
  color: #f0f4f8;
}

@media only screen and (max-width: 1400px) {
  .wrapper {
    width: 900px;
  }
}


.intro .banner {
  width: 100%;
  height: 1000px;
  background: url(../../static/media/intro-bg.444221d9.png) no-repeat 50% 0;
  background-size: 100% 100%;
}

.intro .logo {
  display: block;
  margin-top: 40px;
  width: 250px;
  height: 60px;
  background: url(../../static/media/logo.41d4b42b.png) no-repeat;
}

.intro .headline {
  display: block;
  margin: 80px auto 0;
  width: 950px;
  height: 285px;
}

.product {
  position: relative;
  margin: -400px auto 0;
  width: 1200px;
  height: 660px;
  background: url(../../static/media/product.e5ae63d7.png) no-repeat;
  background-size: 100% 100%;
}

.product .teacher {
  position: absolute;
  top: 0;
  left: 167px;
  z-index: 1;
  width: 840px;
  height: 560px;
}

.product .teacher .tooltip {
  position: absolute;
  top: 340px;
  left: -40px;
  width: 100px;
  height: 50px;
  font-size: 20px;
  line-height: 50px;
  text-align: center;
  color: #0d202f;
  border-radius: 25px 40px 0 25px;
  background: #fff;
  box-shadow: 0 0 5px #cddeec;
}

.product .student {
  position: absolute;
  top: 200px;
  right: 65px;
  z-index: 2;
  width: 270px;
  height: 390px;
}

.product .student .tooltip {
  position: absolute;
  top: 130px;
  left: 260px;
  width: 100px;
  height: 50px;
  font-size: 20px;
  line-height: 50px;
  text-align: center;
  color: #0d202f;
  border-radius: 40px 25px 25px 0;
  background: #fff;
  box-shadow: 0 0 5px #cddeec;
}

.intro-list {
  padding: 40px 0;
  height: 230px;
}

.intro-list li {
  display: inline-block;
  width: 33.33%;
  text-align: center;
}

.intro-item img {
  width: 140px;
  height: 120px;
}

.intro-item h3 {
  margin-top: 15px;
  font-size: 22px;
  line-height: 40px;
  color: #142c44;
}

.intro-item p {
  font-size: 14px;
  line-height: 25px;
  color: #636a7f;
}

@media only screen and (max-width: 1400px) {
  .intro .headline {
    width: 800px;
    height: 240px;
  }

  .product {
    width: 800px;
    height: 440px;
  }

  .product .teacher {
    left: 110px;
    width: 560px;
    height: 380px;
  }

  .product .teacher .tooltip {
    top: 200px;
  }

  .product .student {
    top: 133px;
    right: 42px;
    width: 182px;
    height: 260px;
  }

  .product .student .tooltip {
    left: 175px;
  }
}
.feature {
  position: relative;
  padding: 100px 0;
  height: 720px;
  background: #f8fcff;
}

.feature .circle-red {
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -130px;
  width: 120px;
  height: 260px;
  background: url(data:image/png;base64,) no-repeat;
}

.feature-list {
  margin-top: 80px;
}

.feature-list li {
  width: 33.33%;
  display: inline-block;
  will-change: transform;
}

.feature-list li .feature-box {
  position: relative;
  margin: auto;
  width: 194px;
  text-align: center;
}

.feature-box .feature-image {
  position: relative;
  z-index: 1;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin: 30px auto;
  background: #fff;
  -webkit-transition: box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.feature-box:hover .feature-image {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.feature-box .feature-image img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 44px;
  height: 44px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.feature-box p {
  font-size: 20px;
  line-height: 40px;
}

.feature-point-wrapper {
  position: absolute;
  width: 0;
  left: 50%;
  top: 0;
}

.feature-point-wrapper .point-0 {
  width: 4px;
  height: 4px;
}

.feature-point-wrapper .point-2,
.feature-point-wrapper .point-ring {
  width: 10px;
  height: 10px;
}

.feature-point-wrapper .point-ring {
  border-style: solid;
  border-width: 1px;
  background: transparent !important;
}

.feature-point-wrapper .point-1 {
  width: 6px;
  height: 6px;
}

.feature-point-wrapper .point-3 {
  width: 15px;
  height: 15px;
}

.feature-point-wrapper i {
  display: inline-block;
  border-radius: 100%;
  position: absolute;
  opacity: 0;
  -webkit-transform: translate(0, 30px);
          transform: translate(0, 30px);
}
.scene {
    padding: 150px 0;
    height: 505px;
}

.scene-list li {
    margin-top: 60px;
    display: inline-block;
    width: 33.33%;
}

.scene-list .scene-item {
    margin: 0 auto;
    padding-bottom: 25px;
    width: 340px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 5px 10px 20px #ddd;
    overflow: hidden;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.scene-list li .scene-item:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.scene-item img {
    width: 340px;
    height: 270px;
}

.scene-item h3 {
    margin-top: 10px;
    font-size: 22px;
    line-height: 40px;
    color: #142c44;
}

.scene-item p {
    font-size: 14px;
    line-height: 25px;
    color: #636a7f;
}

.effect {
    position: relative;
    height: 685px;
    padding-bottom: 150px;
}

.effect .circle-green {
    position: absolute;
    top: -130px;
    right: 0;
    width: 120px;
    height: 260px;
    background: url(data:image/png;base64,) no-repeat;
}

.effect-list {
    margin: 0 -25px;
}

.effect-list li {
    display: inline-block;
    width: 50%;
}

.effect-item h3 {
    margin-top: 40px;
    font-size: 30px;
    line-height: 100px;
    text-align: center;
}

.effect-item .effect-img {
    margin: 0 50px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 5px 10px 20px #ddd;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.effect-item:hover .effect-img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.effect-item .effect-img img {
    width: 500px;
}

@media only screen and (max-width: 1400px) {
    .scene-list .scene-item {
        width: 260px;
    }

    .scene-item img {
        width: 260px;
        height: 205px;
    }
    
    .effect-list {
        margin: 0;
    }

    .effect-item h3 {
        font-size: 22px;
    }

    .effect-item .effect-img img {
        width: 300px;
    }

}
.footer {
    width: 100%;
    height: 750px;
    text-align: center;
    color: #fff;
    background: url(../../static/media/footer-bg.2346beeb.png) no-repeat;
    background-size: 100% 100%;
}

.footer .footer-title {
    padding-top: 180px;
    font-size: 40px;
    line-height: 80px;
}

.footer .contact {
    margin-bottom: 60px;
    font-size: 18px;
    line-height: 30px;
}

.footer .contact span {
    font-weight: 700;
    font-size: 22px;
}

.footer .follow {
    font-size: 22px;
    line-height: 80px;
}

.footer .copyright {
    opacity: 0.8;
}

@media only screen and (max-width: 768px) {
    .footer .footer-title {
        font-size: 24px;
        line-height: 50px;
    }
}
.sidebar {
  position: fixed;
  top: 200px;
  right: 40px;
  z-index: 2;
  width: 80px;
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}

.sidebar li {
  position: relative;
  margin-top: 10px;
  padding: 10px;
  width: 60px;
  height: 60px;
  font-size: 20px;
  line-height: 30px;
  color: #fff;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
}

.sidebar li.purple {
  background: #ac66f3;
  box-shadow: 0 5px 10px rgba(129, 128, 228, 0.3);
}

.sidebar li.purple:hover {
  background: #9237ef;
}

.sidebar li.purple a {
  color: #fff;
}

.sidebar li.yellow {
  background: #f5b64c;
  box-shadow: 0 5px 10px rgba(252, 174, 42, 0.3);
}

.sidebar li.yellow:hover {
  background: #f2a21c;
}

.sidebar li.blue {
  background: #46b4f8;
  box-shadow: 0 5px 10px rgba(37, 195, 252, 0.3);
}

.sidebar li.blue:hover {
  background: #20a5f7;
}

.sidebar li .qrcode {
  display: none;
  position: absolute;
  top: -5px;
  left: -170px;
  width: 130px;
  padding: 20px 10px 10px;
  text-align: center;
  border: 1px solid #1486ff;
  border-radius: 10px;
  background: #fff;
}

.sidebar li .qrcode:before,
.sidebar li .qrcode:after {
  content: '';
  position: absolute;
  top: 45px;
  right: -20px;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-left: 10px solid #1486ff;
}

.sidebar li .qrcode:after {
  top: 46px;
  right: -18px;
  border: 9px solid transparent;
  border-left: 9px solid #fff;
}

.sidebar li:hover .qrcode {
  display: block;
}

.sidebar li .qrcode img {
  width: 120px;
  height: 120px;
}

.sidebar li .qrcode p {
  font-size: 18px;
  line-height: 30px;
  color: #333;
}

