.sidebar {
  position: fixed;
  top: 200px;
  right: 40px;
  z-index: 2;
  width: 80px;
  transition: all 0.5s ease-in;
}

.sidebar li {
  position: relative;
  margin-top: 10px;
  padding: 10px;
  width: 60px;
  height: 60px;
  font-size: 20px;
  line-height: 30px;
  color: #fff;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
}

.sidebar li.purple {
  background: #ac66f3;
  box-shadow: 0 5px 10px rgba(129, 128, 228, 0.3);
}

.sidebar li.purple:hover {
  background: #9237ef;
}

.sidebar li.purple a {
  color: #fff;
}

.sidebar li.yellow {
  background: #f5b64c;
  box-shadow: 0 5px 10px rgba(252, 174, 42, 0.3);
}

.sidebar li.yellow:hover {
  background: #f2a21c;
}

.sidebar li.blue {
  background: #46b4f8;
  box-shadow: 0 5px 10px rgba(37, 195, 252, 0.3);
}

.sidebar li.blue:hover {
  background: #20a5f7;
}

.sidebar li .qrcode {
  display: none;
  position: absolute;
  top: -5px;
  left: -170px;
  width: 130px;
  padding: 20px 10px 10px;
  text-align: center;
  border: 1px solid #1486ff;
  border-radius: 10px;
  background: #fff;
}

.sidebar li .qrcode:before,
.sidebar li .qrcode:after {
  content: '';
  position: absolute;
  top: 45px;
  right: -20px;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-left: 10px solid #1486ff;
}

.sidebar li .qrcode:after {
  top: 46px;
  right: -18px;
  border: 9px solid transparent;
  border-left: 9px solid #fff;
}

.sidebar li:hover .qrcode {
  display: block;
}

.sidebar li .qrcode img {
  width: 120px;
  height: 120px;
}

.sidebar li .qrcode p {
  font-size: 18px;
  line-height: 30px;
  color: #333;
}
