.footer {
    width: 100%;
    height: 750px;
    text-align: center;
    color: #fff;
    background: url(../img/footer-bg.png) no-repeat;
    background-size: 100% 100%;
}

.footer .footer-title {
    padding-top: 180px;
    font-size: 40px;
    line-height: 80px;
}

.footer .contact {
    margin-bottom: 60px;
    font-size: 18px;
    line-height: 30px;
}

.footer .contact span {
    font-weight: 700;
    font-size: 22px;
}

.footer .follow {
    font-size: 22px;
    line-height: 80px;
}

.footer .copyright {
    opacity: 0.8;
}

@media only screen and (max-width: 768px) {
    .footer .footer-title {
        font-size: 24px;
        line-height: 50px;
    }
}