.scene {
    padding: 150px 0;
    height: 505px;
}

.scene-list li {
    margin-top: 60px;
    display: inline-block;
    width: 33.33%;
}

.scene-list .scene-item {
    margin: 0 auto;
    padding-bottom: 25px;
    width: 340px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 5px 10px 20px #ddd;
    overflow: hidden;
    transition: all 0.3s ease;
}

.scene-list li .scene-item:hover {
    transform: scale(1.1);
}

.scene-item img {
    width: 340px;
    height: 270px;
}

.scene-item h3 {
    margin-top: 10px;
    font-size: 22px;
    line-height: 40px;
    color: #142c44;
}

.scene-item p {
    font-size: 14px;
    line-height: 25px;
    color: #636a7f;
}

.effect {
    position: relative;
    height: 685px;
    padding-bottom: 150px;
}

.effect .circle-green {
    position: absolute;
    top: -130px;
    right: 0;
    width: 120px;
    height: 260px;
    background: url(../img/circle-green.png) no-repeat;
}

.effect-list {
    margin: 0 -25px;
}

.effect-list li {
    display: inline-block;
    width: 50%;
}

.effect-item h3 {
    margin-top: 40px;
    font-size: 30px;
    line-height: 100px;
    text-align: center;
}

.effect-item .effect-img {
    margin: 0 50px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 5px 10px 20px #ddd;
    transition: all 0.3s ease;
}

.effect-item:hover .effect-img {
    transform: scale(1.1);
}

.effect-item .effect-img img {
    width: 500px;
}

@media only screen and (max-width: 1400px) {
    .scene-list .scene-item {
        width: 260px;
    }

    .scene-item img {
        width: 260px;
        height: 205px;
    }
    
    .effect-list {
        margin: 0;
    }

    .effect-item h3 {
        font-size: 22px;
    }

    .effect-item .effect-img img {
        width: 300px;
    }

}