.feature {
  position: relative;
  padding: 100px 0;
  height: 720px;
  background: #f8fcff;
}

.feature .circle-red {
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -130px;
  width: 120px;
  height: 260px;
  background: url(../img/circle-red.png) no-repeat;
}

.feature-list {
  margin-top: 80px;
}

.feature-list li {
  width: 33.33%;
  display: inline-block;
  will-change: transform;
}

.feature-list li .feature-box {
  position: relative;
  margin: auto;
  width: 194px;
  text-align: center;
}

.feature-box .feature-image {
  position: relative;
  z-index: 1;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin: 30px auto;
  background: #fff;
  transition: box-shadow 0.3s ease-out, transform 0.3s ease-out;
}

.feature-box:hover .feature-image {
  transform: translateY(-5px);
}

.feature-box .feature-image img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 44px;
  height: 44px;
  transform: translate(-50%, -50%);
}

.feature-box p {
  font-size: 20px;
  line-height: 40px;
}

.feature-point-wrapper {
  position: absolute;
  width: 0;
  left: 50%;
  top: 0;
}

.feature-point-wrapper .point-0 {
  width: 4px;
  height: 4px;
}

.feature-point-wrapper .point-2,
.feature-point-wrapper .point-ring {
  width: 10px;
  height: 10px;
}

.feature-point-wrapper .point-ring {
  border-style: solid;
  border-width: 1px;
  background: transparent !important;
}

.feature-point-wrapper .point-1 {
  width: 6px;
  height: 6px;
}

.feature-point-wrapper .point-3 {
  width: 15px;
  height: 15px;
}

.feature-point-wrapper i {
  display: inline-block;
  border-radius: 100%;
  position: absolute;
  opacity: 0;
  transform: translate(0, 30px);
}